"use client"

import { useState, useEffect } from "react"
import SearchBar from "@/components/SearchBar"
import JobCard from "@/components/JobCard"
import { Skeleton } from "@/components/ui/skeleton"
import type { Job } from "@/app/api/jobs/route"

interface JobsResponse {
  success: boolean
  jobs: Job[]
  total: number
  error?: string
}

export default function JobsPage() {
  const [jobs, setJobs] = useState<Job[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [searchFilters, setSearchFilters] = useState({
    search: "",
    category: "all",
    location: "all"
  })

  const fetchJobs = async (filters: typeof searchFilters) => {
    setLoading(true)
    setError(null)
    
    try {
      const params = new URLSearchParams()
      if (filters.search) params.append('search', filters.search)
      if (filters.category !== 'all') params.append('category', filters.category)
      if (filters.location !== 'all') params.append('location', filters.location)

      const response = await fetch(`/api/jobs?${params.toString()}`)
      const data: JobsResponse = await response.json()

      if (data.success) {
        setJobs(data.jobs)
      } else {
        setError(data.error || 'Failed to fetch jobs')
      }
    } catch (err) {
      setError('Network error. Please try again.')
      console.error('Error fetching jobs:', err)
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    fetchJobs(searchFilters)
  }, [])

  const handleSearch = (filters: typeof searchFilters) => {
    setSearchFilters(filters)
    fetchJobs(filters)
  }

  const JobSkeleton = () => (
    <div className="space-y-4">
      {[...Array(6)].map((_, i) => (
        <div key={i} className="border rounded-lg p-6">
          <div className="flex justify-between items-start mb-4">
            <div className="space-y-2 flex-1">
              <Skeleton className="h-6 w-3/4" />
              <Skeleton className="h-4 w-1/2" />
              <Skeleton className="h-4 w-1/3" />
            </div>
            <div className="space-y-2">
              <Skeleton className="h-6 w-20" />
              <Skeleton className="h-6 w-16" />
            </div>
          </div>
          <Skeleton className="h-16 w-full mb-4" />
          <div className="flex justify-between items-center">
            <div className="space-y-1">
              <Skeleton className="h-5 w-24" />
              <Skeleton className="h-3 w-20" />
            </div>
            <Skeleton className="h-10 w-28" />
          </div>
        </div>
      ))}
    </div>
  )

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="mb-8">
        <h1 className="text-4xl font-bold mb-4">Find Your Perfect Job</h1>
        <p className="text-muted-foreground text-lg">
          Discover amazing opportunities for South African teenagers
        </p>
      </div>

      <div className="mb-8">
        <SearchBar onSearch={handleSearch} isLoading={loading} />
      </div>

      {error && (
        <div className="bg-destructive/10 border border-destructive/20 rounded-lg p-4 mb-8">
          <p className="text-destructive font-medium">Error loading jobs</p>
          <p className="text-destructive/80 text-sm mt-1">{error}</p>
          <button 
            onClick={() => fetchJobs(searchFilters)}
            className="text-destructive underline text-sm mt-2 hover:no-underline"
          >
            Try again
          </button>
        </div>
      )}

      <div className="mb-6">
        {!loading && !error && (
          <p className="text-muted-foreground">
            {jobs.length === 0 
              ? "No jobs found matching your criteria. Try adjusting your search filters."
              : `Showing ${jobs.length} job${jobs.length !== 1 ? 's' : ''}`
            }
          </p>
        )}
      </div>

      {loading ? (
        <JobSkeleton />
      ) : jobs.length > 0 ? (
        <div className="grid gap-6">
          {jobs.map((job) => (
            <JobCard key={job.id} job={job} />
          ))}
        </div>
      ) : !error ? (
        <div className="text-center py-16">
          <h3 className="text-xl font-semibold mb-2">No jobs found</h3>
          <p className="text-muted-foreground mb-6">
            Try adjusting your search criteria or check back later for new opportunities.
          </p>
          <button 
            onClick={() => handleSearch({ search: "", category: "all", location: "all" })}
            className="text-primary underline hover:no-underline"
          >
            View all jobs
          </button>
        </div>
      ) : null}
    </div>
  )
}
