"use client"

import { useState } from "react"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"

interface SearchBarProps {
  onSearch: (filters: {
    search: string
    category: string
    location: string
  }) => void
  isLoading?: boolean
}

export default function SearchBar({ onSearch, isLoading = false }: SearchBarProps) {
  const [search, setSearch] = useState("")
  const [category, setCategory] = useState("all")
  const [location, setLocation] = useState("all")

  const handleSearch = () => {
    onSearch({ search, category, location })
  }

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter') {
      handleSearch()
    }
  }

  const categories = [
    { value: "all", label: "All Categories" },
    { value: "retail", label: "Retail & Sales" },
    { value: "food service", label: "Food Service" },
    { value: "tutoring", label: "Tutoring" },
    { value: "admin", label: "Admin Support" }
  ]

  const locations = [
    { value: "all", label: "All Locations" },
    { value: "cape town", label: "Cape Town" },
    { value: "johannesburg", label: "Johannesburg" },
    { value: "durban", label: "Durban" },
    { value: "pretoria", label: "Pretoria" },
    { value: "port elizabeth", label: "Port Elizabeth" },
    { value: "bloemfontein", label: "Bloemfontein" },
    { value: "pietermaritzburg", label: "Pietermaritzburg" }
  ]

  return (
    <div className="bg-card p-6 rounded-lg border shadow-sm">
      <h2 className="text-lg font-semibold mb-4">Find Your Perfect Job</h2>
      <div className="grid md:grid-cols-4 gap-4">
        <div className="md:col-span-2">
          <Input
            type="text"
            placeholder="Search jobs, companies, or keywords..."
            value={search}
            onChange={(e) => setSearch(e.target.value)}
            onKeyPress={handleKeyPress}
            className="w-full"
          />
        </div>
        <div>
          <Select value={category} onValueChange={setCategory}>
            <SelectTrigger>
              <SelectValue placeholder="Category" />
            </SelectTrigger>
            <SelectContent>
              {categories.map((cat) => (
                <SelectItem key={cat.value} value={cat.value}>
                  {cat.label}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
        <div>
          <Select value={location} onValueChange={setLocation}>
            <SelectTrigger>
              <SelectValue placeholder="Location" />
            </SelectTrigger>
            <SelectContent>
              {locations.map((loc) => (
                <SelectItem key={loc.value} value={loc.value}>
                  {loc.label}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
      </div>
      <div className="mt-4 flex gap-2">
        <Button 
          onClick={handleSearch} 
          disabled={isLoading}
          className="px-8"
        >
          {isLoading ? "Searching..." : "Search Jobs"}
        </Button>
        <Button 
          variant="outline" 
          onClick={() => {
            setSearch("")
            setCategory("all")
            setLocation("all")
            onSearch({ search: "", category: "all", location: "all" })
          }}
          disabled={isLoading}
        >
          Clear Filters
        </Button>
      </div>
    </div>
  )
}
